import { configureStore } from "@reduxjs/toolkit";
import { persistReducer, persistStore } from "redux-persist";
import storage from "redux-persist/lib/storage";
import mockEmployed from "data/mockEmployed.js";

/**
 * tat initial du store Redux.
 * @typedef {Object} InitialState
 * @property {Object} employee - L'employ avec les proprits firstName et lastName.
 * @property {string[]} employees - Liste des employs.
 * @property {Object[]} mockEmployed - Liste simule des employs (mocked).
 */
const initialState = {
  employee: {
    firstName: "",
    lastName: "",
  },
  employees: [],
  mockEmployed: mockEmployed,
};

/**
 * Reducer Redux pour grer les actions et mettre  jour l'tat du store.
 * @param {InitialState} state - L'tat initial du store.
 * @param {Object} action - L'action  traiter.
 * @returns {InitialState} Le nouvel tat mis  jour du store.
 */
const reducer = (state = initialState, action) => {
  switch (
    action.type // update eventuel
  ) {
    // case "SET_EMPLOYEE":
    //   return {
    //     ...state,
    //     employee: action.employee,
    //   };
    case "ADD_EMPLOYEE":
      //console.log("Action ADD_EMPLOYEE dispatched with payload:", action.payload);
      return {
        ...state,
        employees: [...state.employees, action.payload],
      };
    case "CLEAR_EMPLOYEES":
      console.log(
        "Action CLEAR_EMPLOYEE dispatched with payload:",
        action.payload
      );
      return {
        ...state,
        employees: [],
      };
    default:
      return state;
  }
};

/**
 * Configuration pour la persistance du store Redux.
 * @typedef {Object} PersistConfig
 * @property {string} key - o les donnes sont stockes.
 * @property {Object} storage - Le mcanisme de stockage (localStorage, etc.).
 * @property {string[]} blacklist - Liste des cls  exclure de la persistance.
 */
const persistConfig = {
  key: "root",
  storage,
  blacklist: ["register"], // Les cls  exclure de la persistance
};

const persistedReducer = persistReducer(persistConfig, reducer);

const store = configureStore({
  reducer: persistedReducer,
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: false, // permet de stocker des objets plus complexes 
      //non serialisable dans le store sans porvoquer d'erreur
    }),
});

export const persistor = persistStore(store);
export default store;
